.table {
    margin-top: 20px;
    overflow-y: scroll;
    color: #6a5d5d;
    background-color: white;
    height: 600px;
  }
  
  .table tr {
    display: flex;
    justify-content: space-between;
  }
  
  .table td {
    padding: 0.5rem;
    border: none;
  }
  
  .table tr:nth-of-type(odd) {
    background-color: #d9d9d9;
  }